import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import uc from './views/nav2/uncomplete.vue'
import pole from "./views/nav3/pole";
import echarts from './views/charts/echarts.vue'
import circle from "./views/nav3/circle";
import other from "./views/nav3/other";
import curtain from "./views/nav3/curtain";
import valance from "./views/nav3/valance";
import yarn from "./views/nav3/yarn";
import address from "./views/nav3/address";
import show_order from "./views/show_order";
import belt from "./views/nav3/belt";
import state0 from "./views/nav2/state0";
import new_order from "./views/new_order";
import state1 from "./views/nav2/state1";
import state2 from "./views/nav2/state2";
import state3 from "./views/nav2/state3";
import state4 from "./views/nav2/state4";
import state5 from "./views/nav2/state5";



let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/show_order',
        component: show_order,
        name: 'show_order',
        hidden: true
    },
    {
        path: '/new_order',
        component: new_order,
        name: 'new_order',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '导航一',
        iconCls: 'el-icon-grape',//图标样式class
        children: [
            { path: '/main', component: Main,iconCls:'el-icon-lollipop', name: '主页', hidden: true },
            {
                path: '/new_order',
                component: new_order,
                name: '新建订单'
            }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '订单',
        iconCls: 'fa fa-id-card-o',
        children: [
            {
                path: '/new_order',
                component: new_order,
                name: '新建订单'
            },
            { path: '/page4', component: Page4, name: '全部订单' },
            { path: '/state0', component: state0,name: '待确认' },
            { path: '/state1', component: state1,name: '待发货' },
            { path: '/state2', component: state2,name: '待完工' },
            { path: '/state3', component: state3,name: '待安装' },
            { path: '/state4', component: state4,name: '待结账' },
            { path: '/state5', component: state5,name: '已完单' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '窗帘仓库',
        iconCls: 'el-icon-lock',
        children: [
            { path: '/curtain', component: curtain, name: '窗帘' },
            { path: '/other', component: other, name: '其他' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '其他物件',
        iconCls: 'el-icon-orange',
        children: [
            { path: '/pole', component: pole, name: '杆子' },
            { path: '/circle', component: circle, name: '圈' },
            { path: '/valance', component: valance, name: '帘头' },
            { path: '/belt', component: belt, name: '带' },
            { path: '/yarn', component: yarn, name: '纱' },
            { path: '/address', component: address, name: '地址管理' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Charts',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/' }
    }
];

export default routes;


